.title-bar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 40px;
  background-color: #111827;
  padding: 0;
  -webkit-app-region: drag;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
}

.title-bar-controls {
  display: flex;
  margin-right: 10px;
  -webkit-app-region: no-drag;
}

.title-bar-button {
  width: 36px;
  height: 36px;
  background: none;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #a0a0a0;
  font-size: 16px;
  cursor: pointer;
  -webkit-app-region: no-drag;
  user-select: none;
}

.title-bar-button:hover {
  color: white;
}

.title-bar-button.minimize span {
  content: "";
  display: block;
  width: 10px;
  height: 1px;
  background-color: currentColor;
}

.title-bar-button.maximize span {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  border: 1px solid currentColor;
  box-sizing: border-box;
}

.title-bar-button.close {
  color: #a0a0a0;
}

.title-bar-button.close:hover {
  color: white;
  background-color: #e81123;
}

.title-bar-button.close span {
  font-size: 20px;
  transform: rotate(45deg);
}

/* Eski stil kaldırma */
.title-bar-spacer, 
.title-bar-logo, 
.title-bar-icon, 
.title-bar-text {
  display: none;
}
