/* Temel değişkenler */
:root {
  --bg-dark: #121418;
  --bg-gradient-start: #1e2128;
  --bg-gradient-end: #141824;
  --text-color: #fff;
  --text-secondary: #a0a0a0;
  --primary-color: #4fc3f7;
  --secondary-color: #7e57c2;
  --accent-color: #00e676;
  --shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
  --glow-primary: 0 0 16px rgba(79, 195, 247, 0.6);
  --neon-blue: #4fc3f7;
  --neon-purple: #7e57c2;
  --neon-green: #00e676;
}

/* Nunito yazı tipi import */
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;500&display=swap');

/* Ana ekran stilleri */
body, .login-root {
  font-family: 'Nunito', sans-serif;
  background: var(--bg-dark);
  color: var(--text-color);
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

/* Arkaplan efekti */
.login-root::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, 
    var(--bg-gradient-start), 
    var(--bg-gradient-end), 
    #1a1e2a, 
    #161a25);
  background-size: 400% 400%;
  clip-path: polygon(
    0 0, 100% 0, 100% 25%, 80% 35%, 65% 25%, 
    45% 35%, 30% 15%, 10% 25%, 0 15%
  );
  opacity: 0.8;
  z-index: 0;
  animation: gradientAnimation 20s ease infinite;
}

@keyframes gradientAnimation {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Yeni Modern Giriş Ekranı */
.login-container {
  position: relative;
  z-index: 1;
  width: 100%;
  height: calc(100vh - 32px); /* Title bar height */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0;
  background: radial-gradient(circle at center, rgba(31, 41, 55, 0.3) 0%, rgba(17, 24, 39, 0) 70%);
}

/* Logo Container */
.wallet-logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.logo-container {
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 auto 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.eg-logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
  filter: drop-shadow(0 0 10px rgba(79, 195, 247, 0.8));
}

/* "Exchange Global" başlığı için stil */
.exchange-global-title {
  font-size: 24px;
  font-weight: 700;
  margin: 0 0 12px;
  text-align: center;
  background: linear-gradient(to right, #fff, #4fc3f7) !important;
  background-clip: text !important;
  -webkit-background-clip: text !important;
  -webkit-text-fill-color: transparent !important;
  letter-spacing: 0.5px;
}

/* "Unlock to Continue" başlığı */
.unlock-title {
  font-size: 20px;
  font-weight: 600;
  margin: 0 0 8px;
  text-align: center;
  color: white;
  letter-spacing: 0.5px;
}

.wallet-status {
  font-size: 13px;
  color: var(--text-secondary);
  margin: 0 0 30px;
  text-align: center;
  position: relative;
  display: inline-block;
  font-weight: 500;
}

.wallet-status::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 2px;
  background: linear-gradient(90deg, var(--neon-blue), var(--neon-purple));
  border-radius: 2px;
  opacity: 0.5;
}

/* Şifre Alanı */
.password-container {
  width: 100%;
  max-width: 320px;
  margin-bottom: 25px;
  position: relative;
}

.password-input-wrapper {
  position: relative;
  width: 100%;
  margin-bottom: 15px;
  border-radius: 6px;
  overflow: hidden;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

.password-icon {
  position: absolute;
  left: 12px;
  top: 50%;
  transform: translateY(-50%);
  width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 2;
}

.eye-icon {
  width: 18px;
  height: 18px;
  fill: rgba(255, 255, 255, 0.6);
  transition: fill 0.3s ease;
}

.password-icon:hover .eye-icon {
  fill: var(--neon-blue);
}

.modern-input {
  width: 100%;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 6px;
  color: white;
  font-size: 14px;
  font-family: 'Nunito', sans-serif;
  font-weight: 500;
  padding: 12px 12px 12px 40px;
  transition: all 0.3s ease;
}

.modern-input:focus {
  outline: none;
  border-color: rgba(79, 195, 247, 0.4);
  background: rgba(255, 255, 255, 0.07);
}

.modern-input::placeholder {
  color: rgba(255, 255, 255, 0.4);
  font-family: 'Nunito', sans-serif;
  font-weight: 400;
}

.error-message {
  color: #ff5252;
  font-size: 13px;
  margin-top: 8px;
  text-align: center;
  animation: fadeIn 0.3s ease;
  font-weight: 500;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-5px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Eylem butonları */
.action-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 10px;
}

.action-button {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 6px;
  color: var(--text-secondary);
  padding: 8px 14px;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 6px;
}

.action-button:hover {
  background: rgba(255, 255, 255, 0.07);
  transform: translateY(-1px);
}

.action-icon {
  width: 14px;
  height: 14px;
  fill: currentColor;
  opacity: 0.8;
}

.help-button:hover {
  color: var(--neon-blue);
  border-color: rgba(79, 195, 247, 0.3);
}

.restore-button:hover {
  color: var(--neon-purple);
  border-color: rgba(126, 87, 194, 0.3);
}

.quit-button:hover {
  color: #ff5252;
  border-color: rgba(255, 82, 82, 0.3);
}

/* Login button */
.login-button {
  display: block;
  width: 100%;
  padding: 12px;
  margin-top: 20px;
  background: linear-gradient(90deg, #3898ff, #5e7ce2);
  border: none;
  border-radius: 6px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  font-family: 'Nunito', sans-serif;
}

.login-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(56, 152, 255, 0.3);
}

.login-button:active {
  transform: translateY(0);
  box-shadow: 0 2px 6px rgba(56, 152, 255, 0.2);
}

.login-button:disabled {
  background: linear-gradient(90deg, rgba(56, 152, 255, 0.5), rgba(94, 124, 226, 0.5));
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* Removed styles */
.company-title {
  display: none;
}