.debug-toggle-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: rgba(0, 162, 255, 0.2);
  border: 1px solid rgba(0, 162, 255, 0.5);
  color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 9999;
  transition: all 0.2s ease;
}

.debug-toggle-button:hover {
  background-color: rgba(0, 162, 255, 0.3);
  transform: scale(1.1);
}

.debug-panel {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 600px;
  max-width: calc(100% - 40px);
  max-height: 80vh;
  background-color: var(--bg-secondary);
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  z-index: 9999;
  overflow: hidden;
  border: 1px solid rgba(0, 162, 255, 0.3);
}

.debug-header {
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.debug-header h3 {
  margin: 0;
  color: var(--primary-color);
  font-size: 1.2rem;
}

.close-button {
  background: none;
  border: none;
  color: var(--text-secondary);
  font-size: 1.2rem;
  cursor: pointer;
}

.close-button:hover {
  color: var(--text-color);
}

.debug-tabs {
  display: flex;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.tab-button {
  flex: 1;
  padding: 0.8rem;
  background: none;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  position: relative;
  transition: all 0.2s ease;
}

.tab-button.active {
  color: var(--primary-color);
}

.tab-button.active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 20%;
  width: 60%;
  height: 3px;
  background-color: var(--primary-color);
  border-radius: 3px 3px 0 0;
}

.tab-button:hover:not(.active) {
  background-color: rgba(255, 255, 255, 0.05);
}

.debug-content {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
}

.tab-content {
  margin-bottom: 1rem;
}

.tab-content h4 {
  margin-top: 0;
  margin-bottom: 1rem;
  color: var(--text-color);
}

.action-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.action-buttons button {
  padding: 0.5rem 1rem;
  background-color: rgba(0, 162, 255, 0.1);
  border: 1px solid rgba(0, 162, 255, 0.2);
  border-radius: 4px;
  color: var(--primary-color);
  cursor: pointer;
  transition: all 0.2s ease;
}

.action-buttons button:hover {
  background-color: rgba(0, 162, 255, 0.2);
}

.action-buttons button.warning-button {
  background-color: rgba(255, 193, 7, 0.1);
  border-color: rgba(255, 193, 7, 0.2);
  color: #FFC107;
}

.action-buttons button.warning-button:hover {
  background-color: rgba(255, 193, 7, 0.2);
}

.action-buttons button.danger-button {
  background-color: rgba(255, 82, 82, 0.1);
  border-color: rgba(255, 82, 82, 0.2);
  color: #FF5252;
}

.action-buttons button.danger-button:hover {
  background-color: rgba(255, 82, 82, 0.2);
}

.storage-content {
  background-color: rgba(0, 0, 0, 0.2);
  padding: 1rem;
  border-radius: 8px;
  margin-top: 1rem;
}

.storage-content h5 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.storage-content pre {
  margin: 0;
  font-family: 'Courier New', monospace;
  font-size: 0.85rem;
  white-space: pre-wrap;
  word-break: break-all;
  color: var(--text-color);
}

.test-result {
  margin-top: 1rem;
  padding: 1rem;
  border-radius: 8px;
}

.test-result.success {
  background-color: rgba(0, 200, 83, 0.1);
  border-left: 4px solid #00C853;
}

.test-result.error {
  background-color: rgba(255, 82, 82, 0.1);
  border-left: 4px solid #FF5252;
}

.test-result h5 {
  margin-top: 0;
  margin-bottom: 0.5rem;
  color: var(--text-color);
}

.test-result pre {
  margin: 0;
  font-family: 'Courier New', monospace;
  font-size: 0.85rem;
  white-space: pre-wrap;
  word-break: break-all;
  max-height: 200px;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, 0.2);
  padding: 0.5rem;
  border-radius: 4px;
  color: var(--text-color);
}

.loading-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.spinner {
  border: 3px solid rgba(var(--primary-rgb), 0.3);
  border-radius: 50%;
  border-top: 3px solid var(--primary-color);
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
