.send-modal {
  width: 100%;
  max-width: 500px;
}

.send-modal .modal-header h2 {
  display: flex;
  align-items: center;
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
}

.currency-icon {
  width: 26px;
  height: 26px;
  margin-right: 10px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.05);
  background-color: var(--bg-primary);
  padding: 2px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: var(--text-secondary);
  font-size: 13px;
}

.form-group input {
  width: 100%;
  padding: 12px 15px;
  background-color: var(--bg-tertiary);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  color: var(--text-primary);
  font-size: 14px;
  transition: all 0.3s ease;
  font-family: 'Nunito', sans-serif;
}

.form-group input:focus {
  border-color: var(--accent-blue);
  outline: none;
  box-shadow: 0 0 0 2px rgba(88, 166, 255, 0.2);
}

.form-group input.invalid {
  border-color: var(--accent-red);
  box-shadow: 0 0 0 2px rgba(248, 81, 73, 0.2);
}

.field-error {
  color: var(--accent-red);
  font-size: 12px;
  margin-top: 6px;
  margin-left: 8px;
}

.amount-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.available-balance {
  font-size: 12px;
  color: var(--text-secondary);
}

.balance-value {
  font-weight: 600;
  color: var(--accent-blue);
}

.amount-input-container {
  position: relative;
  display: flex;
  align-items: center;
}

.amount-suffix {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  display: flex;
  align-items: center;
  padding-right: 15px;
}

.currency-symbol {
  color: var(--text-secondary);
  font-weight: 600;
  margin-right: 10px;
}

.max-button {
  background-color: rgba(88, 166, 255, 0.1);
  color: var(--accent-blue);
  border: 1px solid rgba(88, 166, 255, 0.3);
  border-radius: 4px;
  padding: 3px 8px;
  font-size: 11px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.max-button:hover:not(:disabled) {
  background-color: rgba(88, 166, 255, 0.2);
}

.max-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.fee-container {
  background-color: var(--bg-tertiary);
  border-radius: 8px;
  padding: 15px;
  margin-top: 20px;
  border: 1px solid var(--border-color);
}

.fee-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.fee-row:last-child {
  margin-bottom: 0;
}

.fee-row.total {
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px dashed var(--border-color);
}

.fee-label {
  color: var(--text-secondary);
  font-size: 13px;
}

.fee-value {
  font-weight: 600;
  color: var(--text-primary);
  font-size: 13px;
}

.token-fee-note {
  display: flex;
  align-items: flex-start;
  margin-top: 12px;
  font-size: 12px;
  color: var(--text-secondary);
  line-height: 1.4;
  background-color: rgba(227, 179, 65, 0.1);
  border: 1px solid rgba(227, 179, 65, 0.2);
  border-radius: 6px;
  padding: 10px 12px;
}

.token-fee-note i {
  margin-right: 8px;
  margin-top: 2px;
  color: var(--accent-yellow);
}

.spinner-small {
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  border-top: 2px solid var(--text-primary);
  width: 16px;
  height: 16px;
  animation: spin 1s linear infinite;
  margin-right: 8px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
