:root {
  --primary-color: #00a2ff;
  --primary-rgb: 0, 162, 255;
  --secondary-color: #6e42e5;
  --secondary-rgb: 110, 66, 229;
  --accent-color: #00ffb9;
  --accent-rgb: 0, 255, 185;
  
  --bg-dark: #1a1b20;
  --bg-darker: #121318;
  --bg-secondary: #282a36;
  
  --text-color: #ffffff;
  --text-secondary: #a0a0a0;
  
  --shadow: 0 8px 20px rgba(0, 0, 0, 0.25);
  --shadow-sm: 0 4px 6px rgba(0, 0, 0, 0.1);
  --shadow-lg: 0 12px 30px rgba(0, 0, 0, 0.4);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Nunito', sans-serif;
  background-color: var(--bg-dark);
  color: var(--text-color);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-content {
  flex: 1;
  padding-top: 40px; /* TitleBar yüksekliği */
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.app-loading {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--bg-dark);
}

.spinner {
  border: 4px solid rgba(var(--primary-rgb), 0.3);
  border-radius: 50%;
  border-top: 4px solid var(--primary-color);
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.app-loading p {
  color: var(--text-secondary);
}

/* Card Stili */
.card {
  background-color: var(--bg-secondary);
  border-radius: 12px;
  box-shadow: var(--shadow);
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.card-title {
  font-size: 1.2rem;
  color: var(--text-color);
  font-weight: 600;
}

/* Buton Stilleri */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem 1rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
  outline: none;
}

.btn-primary {
  background: linear-gradient(45deg, var(--primary-color), var(--secondary-color));
  color: white;
}

.btn-secondary {
  background: transparent;
  color: var(--primary-color);
  border: 1px solid var(--primary-color);
}

.btn-accent {
  background-color: var(--accent-color);
  color: var(--bg-dark);
}

.btn-ghost {
  background: transparent;
  color: var(--text-secondary);
}

.btn-sm {
  padding: 0.3rem 0.6rem;
  font-size: 0.85rem;
}

.btn-lg {
  padding: 0.8rem 1.5rem;
  font-size: 1.1rem;
}

.btn-icon {
  width: 36px;
  height: 36px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.btn-icon i {
  font-size: 1rem;
}

.btn-primary:hover:not(:disabled) {
  background: linear-gradient(45deg, var(--secondary-color), var(--primary-color));
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(var(--primary-rgb), 0.4);
}

.btn-secondary:hover:not(:disabled) {
  background-color: rgba(var(--primary-rgb), 0.1);
  transform: translateY(-2px);
}

.btn-accent:hover:not(:disabled) {
  filter: brightness(110%);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(var(--accent-rgb), 0.4);
}

.btn-ghost:hover:not(:disabled) {
  color: var(--text-color);
  background-color: rgba(255, 255, 255, 0.05);
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Form Elemanları */
.form-group {
  margin-bottom: 1.2rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

.form-control {
  width: 100%;
  padding: 0.8rem 1rem;
  background-color: var(--bg-dark);
  border: 1px solid var(--bg-darker);
  border-radius: 8px;
  color: var(--text-color);
  font-size: 1rem;
  transition: all 0.3s ease;
}

.form-control:focus {
  border-color: var(--primary-color);
  outline: none;
  box-shadow: 0 0 0 2px rgba(var(--primary-rgb), 0.2);
}

/* Grid Sistemi */
.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.col {
  flex: 1;
  padding: 0 15px;
}

/* Yardımcı Sınıflar */
.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.mt-1 { margin-top: 0.5rem; }
.mt-2 { margin-top: 1rem; }
.mt-3 { margin-top: 1.5rem; }
.mt-4 { margin-top: 2rem; }

.mb-1 { margin-bottom: 0.5rem; }
.mb-2 { margin-bottom: 1rem; }
.mb-3 { margin-bottom: 1.5rem; }
.mb-4 { margin-bottom: 2rem; }

.ml-1 { margin-left: 0.5rem; }
.ml-2 { margin-left: 1rem; }

.mr-1 { margin-right: 0.5rem; }
.mr-2 { margin-right: 1rem; }

.p-1 { padding: 0.5rem; }
.p-2 { padding: 1rem; }
.p-3 { padding: 1.5rem; }
.p-4 { padding: 2rem; }

.d-flex { display: flex; }
.flex-col { flex-direction: column; }
.justify-center { justify-content: center; }
.justify-between { justify-content: space-between; }
.align-center { align-items: center; }
.align-start { align-items: flex-start; }

/* Responsive */
@media (max-width: 768px) {
  .row {
    flex-direction: column;
  }
  
  .col {
    width: 100%;
    margin-bottom: 1rem;
  }
}

/* Offline Indicator */
.offline-indicator {
  position: fixed;
  bottom: 20px;
  left: 20px;
  background-color: rgba(255, 193, 7, 0.9);
  color: #000;
  padding: 8px 12px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.85rem;
  font-weight: 600;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  z-index: 9000;
}

.offline-indicator i {
  font-size: 1rem;
}

/* Notification */
.app-notification {
  position: fixed;
  top: 60px;
  right: 20px;
  padding: 12px 16px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 300px;
  max-width: 400px;
  background-color: var(--bg-secondary);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
  z-index: 9999;
  animation: notification-slide-in 0.3s ease forwards;
}

@keyframes notification-slide-in {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.app-notification.success {
  border-left: 4px solid #00C853;
}

.app-notification.warning {
  border-left: 4px solid #FFC107;
}

.app-notification.error {
  border-left: 4px solid #FF5252;
}

.app-notification.info {
  border-left: 4px solid var(--primary-color);
}

.notification-content {
  display: flex;
  align-items: center;
  gap: 10px;
}

.notification-content i {
  font-size: 1.2rem;
}

.app-notification.success i {
  color: #00C853;
}

.app-notification.warning i {
  color: #FFC107;
}

.app-notification.error i {
  color: #FF5252;
}

.app-notification.info i {
  color: var(--primary-color);
}

.notification-close {
  background: none;
  border: none;
  color: var(--text-secondary);
  cursor: pointer;
  font-size: 1rem;
}

.notification-close:hover {
  color: var(--text-color);
}

/* Yükleniyor İndikatörü */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.loading-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--bg-secondary);
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.loading-text {
  margin-top: 1rem;
  color: var(--text-color);
}
